

/* Content grid
/* ---------------------------------------------------------- */

.gh-canvas,
.kg-width-full.kg-content-wide {
    --gap: 4vw;
    --main: min(var(--content-width, 720px), 100% - var(--gap) * 2);
    --wide: minmax(0, calc((var(--container-width, 1200px) - var(--content-width, 720px)) / 2));
    --full: minmax(var(--gap), 1fr);

    display: grid;
    grid-template-columns:
        [full-start] var(--full)
        [wide-start] var(--wide)
        [main-start] var(--main) [main-end]
        var(--wide) [wide-end]
        var(--full) [full-end];
}

.gh-canvas > * {
    grid-column: main-start / main-end;
}

.kg-width-wide,
.kg-content-wide > div {
    grid-column: wide-start / wide-end;
}

.kg-width-full {
    grid-column: full-start / full-end;
}

.kg-width-full img {
    width: 100%;
}


/* Content & Typography
/* ---------------------------------------------------------- */

.gh-content > * + * {
    margin-top: 4vmin;
}

.gh-content [id]:not(:first-child) {
    margin: 1.5em 0 0;
}

.gh-content > [id] + * {
    margin-top: 1.5rem;
}

.gh-content [id] + .kg-card,
.gh-content blockquote + .kg-card {
    margin-top: 6vmin;
}

.gh-canvas > blockquote,
.gh-canvas > ol,
.gh-canvas > ul,
.gh-canvas > dl,
.gh-canvas > p {
    font-family: var(--font-serif);
    font-weight: 400;
    font-size: 2rem;
    line-height: 1.6em;
}

.gh-content > ul,
.gh-content > ol,
.gh-content > dl {
    padding-left: 1.9em;
}

.gh-content hr {
    margin-top: 6vmin;
}

.gh-content hr + * {
    margin-top: 6vmin !important;
}

.gh-content blockquote {
    position: relative;
    font-style: italic;
}

.gh-content blockquote::before {
    content: "";
    position: absolute;
    left: -1.5em;
    top: 0;
    bottom: 0;
    width: 0.3rem;
    background: var(--color-primary);
}

@media (max-width: 650px) {
    .gh-canvas blockquote,
    .gh-canvas ol,
    .gh-canvas ul,
    .gh-canvas dl,
    .gh-canvas p {
        font-size: 1.8rem;
    }

    .gh-content blockquote::before {
        left: -4vmin;
    }
}


/* Cards
/* ---------------------------------------------------------- */

.gh-content :not(.kg-card):not([id]) + .kg-card {
    margin-top: 6vmin;
}

.gh-content .kg-card + :not(.kg-card) {
    margin-top: 6vmin;
}

figcaption {
    padding: 1.5rem 1.5rem 0;
    text-align: center;
    color: rgba(0,0,0,0.5);
    font-weight: 600;
    font-size: 1.3rem;
    line-height: 1.4em;
}

figcaption strong {
    color: rgba(0,0,0,0.8);
}

.gh-canvas :not(pre) code {
    vertical-align: middle;
    padding: 0.15em 0.4em 0.15em;
    border: #e1eaef 1px solid;
    font-weight: 400 !important;
    font-size: 0.9em;
    line-height: 1em;
    color: #dc0050;
    background: #f0f6f9;
    border-radius: 0.25em;
}

.gh-canvas > pre {
    overflow: scroll;
    padding: 16px 20px;
    background: rgba(255,255,255,0.8);
    border-radius: 5px;
    box-shadow: 0 2px 6px -2px rgba(0,0,0,.1), 0 0 1px rgba(0,0,0,.4);
}

.kg-embed-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

/* Default to 16 / 9 aspect ratio for YouTube & Vimeo embeds  */
.kg-embed-card > :where(iframe[src*="youtube.com"],iframe[src*="vimeo.com"]) {
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9;
}

.kg-image-card img {
    margin: auto;
}


/* Card captions
/* ---------------------------------------------------------- */

.kg-width-full.kg-card-hascaption {
    display: grid;
    grid-template-columns: inherit;
}

.kg-width-wide.kg-card-hascaption img {
    grid-column: wide-start / wide-end;
}
.kg-width-full.kg-card-hascaption img {
    grid-column: 1 / -1;
}

.kg-width-full.kg-card-hascaption figcaption {
    grid-column: main-start / main-end;
}


/* Tables
/* ---------------------------------------------------------- */

.gh-content table {
    border-collapse: collapse;
    width: 100%;
}

.gh-content th {
    padding: 0.5em 0.8em;
    text-align: left;
    font-size: .75em;
    text-transform: uppercase;
}

.gh-content td {
    padding: 0.4em 0.7em;
}

.gh-content tbody tr:nth-child(2n + 1) {
    background-color: rgba(0,0,0,0.1);
    padding: 1px;
}

.gh-content tbody tr:nth-child(2n + 2) td:last-child {
    box-shadow:
        inset 1px 0 rgba(0,0,0,0.1),
        inset -1px 0 rgba(0,0,0,0.1);
}

.gh-content tbody tr:nth-child(2n + 2) td {
    box-shadow: inset 1px 0 rgba(0,0,0,0.1);
}

.gh-content tbody tr:last-child {
    border-bottom: 1px solid rgba(0,0,0,.1);
}

/* Cyberpunk Content Styles */

/* Post cards */
.gh-card {
    background: var(--color-bg-card);
    border: 2px solid var(--color-border-primary);
    border-radius: var(--radius-lg);
    overflow: hidden;
    transition: var(--transition-normal);
    position: relative;
    box-shadow: var(--shadow-dark);
}

.gh-card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 2px;
    background: linear-gradient(90deg, var(--color-neon-cyan), var(--color-neon-pink), var(--color-neon-green));
    opacity: 0;
    transition: var(--transition-normal);
}

.gh-card:hover {
    border-color: var(--color-border-accent);
    box-shadow: var(--shadow-glow);
    transform: translateY(-5px);
}

.gh-card:hover::before {
    opacity: 1;
    animation: scanner 2s linear infinite;
}

.gh-card-link {
    display: flex;
    flex-direction: column;
    height: 100%;
    text-decoration: none;
    color: inherit;
}

.gh-card-image {
    position: relative;
    width: 100%;
    height: 200px;
    overflow: hidden;
    background: var(--color-bg-secondary);
}

.gh-card-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--transition-normal);
}

.gh-card:hover .gh-card-image img {
    transform: scale(1.05);
    filter: brightness(1.2) contrast(1.1);
}

.gh-card-content {
    padding: var(--spacing-lg);
    flex: 1;
    display: flex;
    flex-direction: column;
}

.gh-card-title {
    font-family: 'Orbitron', monospace;
    font-size: var(--font-size-lg);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--color-text-primary);
    margin: 0 0 var(--spacing-md) 0;
    line-height: 1.3;
    text-shadow: 0 0 10px var(--color-neon-cyan);
    transition: var(--transition-normal);
}

.gh-card:hover .gh-card-title {
    color: var(--color-neon-pink);
    text-shadow: 0 0 15px var(--color-neon-pink);
}

.gh-card-excerpt {
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-sm);
    color: var(--color-text-secondary);
    line-height: 1.6;
    margin: 0 0 var(--spacing-lg) 0;
    flex: 1;
}

.gh-card-meta {
    display: flex;
    align-items: center;
    gap: var(--spacing-md);
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-xs);
    color: var(--color-text-muted);
    margin-top: auto;
}

.gh-card-author {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
}

.gh-card-author img {
    width: 24px;
    height: 24px;
    border-radius: 50%;
    border: 1px solid var(--color-border-accent);
    box-shadow: 0 0 5px var(--color-neon-cyan);
}

.gh-card-date {
    color: var(--color-neon-green);
    text-shadow: 0 0 5px var(--color-neon-green);
}

/* Post content */
.gh-article {
    background: var(--color-bg-card);
    border: 2px solid var(--color-border-accent);
    border-radius: var(--radius-lg);
    padding: var(--spacing-3xl);
    margin: var(--spacing-3xl) 0;
    box-shadow: var(--shadow-dark);
    position: relative;
    overflow: hidden;
}

.gh-article::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--color-neon-cyan), var(--color-neon-pink), var(--color-neon-green));
    animation: scanner 4s linear infinite;
}

.gh-article-header {
    margin-bottom: var(--spacing-3xl);
    text-align: center;
    position: relative;
}

.gh-article-title {
    font-family: 'Orbitron', monospace;
    font-size: var(--font-size-4xl);
    font-weight: 900;
    text-transform: uppercase;
    letter-spacing: 3px;
    color: var(--color-text-primary);
    margin: 0 0 var(--spacing-lg) 0;
    line-height: 1.2;
    text-shadow: 0 0 20px var(--color-neon-cyan), 0 0 40px var(--color-neon-cyan);
}

.gh-article-excerpt {
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-lg);
    color: var(--color-text-secondary);
    line-height: 1.6;
    margin: 0 0 var(--spacing-xl) 0;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
}

.gh-article-meta {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: var(--spacing-lg);
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-sm);
    color: var(--color-text-muted);
    margin-bottom: var(--spacing-xl);
}

.gh-article-author {
    display: flex;
    align-items: center;
    gap: var(--spacing-sm);
}

.gh-article-author img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid var(--color-border-accent);
    box-shadow: var(--shadow-neon-cyan);
}

.gh-article-date {
    color: var(--color-neon-green);
    text-shadow: 0 0 5px var(--color-neon-green);
}

.gh-article-reading-time {
    color: var(--color-neon-pink);
    text-shadow: 0 0 5px var(--color-neon-pink);
}

.gh-article-image {
    width: 100%;
    max-width: 800px;
    height: 400px;
    margin: 0 auto var(--spacing-3xl);
    border-radius: var(--radius-lg);
    overflow: hidden;
    box-shadow: var(--shadow-dark);
    position: relative;
}

.gh-article-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--transition-normal);
}

.gh-article-image:hover img {
    transform: scale(1.02);
    filter: brightness(1.1) contrast(1.05);
}

.gh-article-content {
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-base);
    line-height: 1.8;
    color: var(--color-text-secondary);
    max-width: 800px;
    margin: 0 auto;
}

.gh-article-content h2 {
    font-family: 'Orbitron', monospace;
    font-size: var(--font-size-2xl);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: var(--color-neon-pink);
    margin: var(--spacing-2xl) 0 var(--spacing-lg) 0;
    text-shadow: 0 0 15px var(--color-neon-pink);
    border-left: 4px solid var(--color-neon-pink);
    padding-left: var(--spacing-lg);
}

.gh-article-content h3 {
    font-family: 'Orbitron', monospace;
    font-size: var(--font-size-xl);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--color-neon-green);
    margin: var(--spacing-xl) 0 var(--spacing-md) 0;
    text-shadow: 0 0 10px var(--color-neon-green);
}

.gh-article-content h4, .gh-article-content h5, .gh-article-content h6 {
    font-family: 'Orbitron', monospace;
    font-size: var(--font-size-lg);
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--color-text-primary);
    margin: var(--spacing-lg) 0 var(--spacing-md) 0;
    text-shadow: 0 0 8px var(--color-neon-cyan);
}

.gh-article-content p {
    margin: 0 0 var(--spacing-lg) 0;
}

.gh-article-content a {
    color: var(--color-text-link);
    text-decoration: none;
    border-bottom: 1px solid var(--color-neon-cyan);
    transition: var(--transition-normal);
}

.gh-article-content a:hover {
    color: var(--color-text-link-hover);
    border-bottom-color: var(--color-neon-pink);
    text-shadow: 0 0 5px var(--color-neon-pink);
}

.gh-article-content ul, .gh-article-content ol {
    margin: var(--spacing-lg) 0;
    padding-left: var(--spacing-xl);
}

.gh-article-content li {
    margin-bottom: var(--spacing-sm);
}

.gh-article-content strong {
    color: var(--color-neon-yellow);
    text-shadow: 0 0 5px var(--color-neon-yellow);
}

.gh-article-content em {
    color: var(--color-neon-orange);
    text-shadow: 0 0 5px var(--color-neon-orange);
}

/* Tags */
.gh-article-tags {
    display: flex;
    flex-wrap: wrap;
    gap: var(--spacing-sm);
    margin: var(--spacing-xl) 0;
    justify-content: center;
}

.gh-article-tag {
    display: inline-block;
    padding: var(--spacing-xs) var(--spacing-md);
    background: var(--color-bg-secondary);
    border: 1px solid var(--color-border-accent);
    border-radius: var(--radius-sm);
    color: var(--color-neon-cyan);
    font-family: 'Share Tech Mono', monospace;
    font-size: var(--font-size-xs);
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    transition: var(--transition-normal);
    box-shadow: 0 0 5px var(--color-neon-cyan);
}

.gh-article-tag:hover {
    background: var(--color-bg-tertiary);
    border-color: var(--color-neon-pink);
    color: var(--color-neon-pink);
    box-shadow: 0 0 10px var(--color-neon-pink);
    transform: translateY(-2px);
}

/* Responsive design */
@media (max-width: 768px) {
    .gh-article {
        padding: var(--spacing-xl);
        margin: var(--spacing-xl) 0;
    }
    
    .gh-article-title {
        font-size: var(--font-size-3xl);
        letter-spacing: 2px;
    }
    
    .gh-article-excerpt {
        font-size: var(--font-size-base);
    }
    
    .gh-article-meta {
        flex-direction: column;
        gap: var(--spacing-md);
    }
    
    .gh-article-image {
        height: 250px;
        margin-bottom: var(--spacing-xl);
    }
    
    .gh-card-content {
        padding: var(--spacing-md);
    }
    
    .gh-card-title {
        font-size: var(--font-size-base);
    }
    
    .gh-card-meta {
        flex-direction: column;
        align-items: flex-start;
        gap: var(--spacing-sm);
    }
}

@media (max-width: 480px) {
    .gh-article {
        padding: var(--spacing-lg);
        margin: var(--spacing-lg) 0;
    }
    
    .gh-article-title {
        font-size: var(--font-size-2xl);
        letter-spacing: 1px;
    }
    
    .gh-article-image {
        height: 200px;
    }
    
    .gh-card-image {
        height: 150px;
    }
}
